import React, { Fragment } from "react";
import OrderCardProducts from "./OrderCardProducts";

const OrderCard = ({ orders, loading }) => {

  const returnPaidStatus = (code) => {
    if (code === "1") return "  ";
    else return "   ";
  };

  const returnDeliveryMethod = (code) => {
    switch (code) {
      case "1":
        return "   ";
      case "2":
        return " ";
      case "4":
        return " , . ";
      case "5":
        return " PickPoint";
      case "8":
        return "  ";
      case "10":
        return "   (  )";
      default:
        return "";
    }
  }

  const returnStatusName = (code) => {
    switch (code) {
      case "1":
        return "";
      case "2":
        return "  ";
      case "3":
        return " ";
      case "4":
        return " ";
      case "5":
        return "  ";
      case "6":
        return "  ";
      case "7":
        return "  ";
      case "8":
        return "";
      case "9":
        return "   ";
      case "10":
        return " ";
      case "11":
        return "  ";
      case "12":
        return ".  ";
      case "13":
        return "";
      default:
        return "";
    }
  }

  const returnAids = (OrderItems) => {
    const aids = [];
    OrderItems.map(item => (
      aids.push(item.OrderItems_Item[0].aID[0])));
    return aids;
  }

  const returnTrackData = (data) => {
    if (!data) return " ,   ";
    return (
      <Fragment>
        <p className="black-text"> : {data[0].PostCode[0]}</p>
        <p className="black-text">C : {data[0].PostStatusName[0]}</p>
        <p className="black-text"> : {data[0].TrackingUrl[0]}</p>
      </Fragment>
    );
  }

  const dataMap = () => {
    return orders.map((item, index) => (
      <div key={index} className="card">
        <div className="card-content white-text">
          <span className="card-title black-text"> : <b className="orange-text">{item.Orders_Item[0].orderID[0]}</b></span>
          <table className="striped black-text">
            <tbody>
              <tr>
                <td>  </td>
                <td>{item.Orders_Item[0].ExtDateOfAdded[0]}</td>
              </tr>
              <tr>
                <td>  </td>
                <td>{returnPaidStatus(item.Orders_Item[0].ExtOrderPaid[0])}</td>
              </tr>
              <tr>
                <td>  </td>
                <td>{item.Orders_Item[0].ExtOrderTotal[0]}</td>
              </tr>
              <tr>
                <td> </td>
                <td>{item.Orders_Item[0].ExtDeliveryCost[0]}</td>
              </tr>
              <tr>
                <td> </td>
                <td>{returnDeliveryMethod(item.Orders_Item[0].dsDelivery[0])}</td>
              </tr>
              <tr>
                <td>     </td>
                <td>{item.Orders_Item[0].pickupDate[0]}</td>
              </tr>
              <tr>
                <td>  </td>
                <td>{returnStatusName(item.Orders_Item[0].status[0])}</td>
              </tr>
              <tr>
                <td> </td>
                <td>{returnTrackData(item.Orders_Item[0].postData)}</td>
              </tr>
            </tbody>
          </table>

          <OrderCardProducts aids={returnAids(item.Orders_Item[0].OrderItems)} />
        </div>
      </div>
    ));
  }

  return orders ? (
    <div className="row">
      <div className="col s12">
        {loading ? <div className="progress">
          <div className="indeterminate"></div>
        </div> : dataMap()}
      </div>
    </div>
  ) : <Fragment />;
}

export default OrderCard;